
<template >
    <section>
        <div class="col-md-12 p-0 mt-2">
            <div class="theme-primary partition-full">
                <span class="p-name text-white">Timesheet</span>
                <span class="p-actions float-right">
                    <a id="AddManually" href="javascript:;"
                        class="p-action-btn text-white addman btn-info"><span>Export</span></a>
                    <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"><span>This
                            week</span></a>
                            <button class="fa fa-trash-o text-danger"></button>
                </span>
            </div>
        </div>
        <div class="listing py-3 px-3">
            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons1" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-slotdate="{ data }">
                    <div class="col-12 input-group timesheetdatepicker bg-white border-0 p-0">
                        <div class="form-control">
                            <label>{{ $t('DateRange') }}:</label>&nbsp;<a id="aDateLable" class="pr-2 text-dark"
                                style="cursor:default">{{ DisplayWeekDate }}</a>
                            <input id="date_range" v-model="dateselect" value="2023/12/12 - 2023/12/12" name="date_range"
                                type="date" @input="GetStartEndDates"
                                class="textfield2 ie_crossicon_none ToDate form-control p-1 hasdatetimepicker notvue"
                                autocomplete="off" data-toggle="datetimepicker" :title="$t('DateRange')"
                                :placeholder="$t('DateRange')">
                        </div>
                    </div>
                </template>
                <template v-slot:tgslot-slotbutton="{ data }">
                    <button class="btn btn-success" @click="FetchData" :title="$t('ShowTimeSheet')">{{ $t('ShowTimeSheet')
                    }} </button>
                </template>
                <template v-slot:tgslot-slotstartenddate="{ data }">
                    <p style="top:-74px;margin-left: 104px;">{{ DisplayWeekDate }}</p>
                </template>
            </dynamic-form>
            <!-- <dynamic-form lang="en" v-if="DataLoaded" :schema="ListSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-TimeSheetTable="{ data }">
                    <a id="btnAddNewEntry" class="btn btn-success formbtn mr-2 text-dark" v-on:click="showAddTaskModal"
                        name="btnAddNewEntry" title='Add New Entry' style="float: right;"><i class="fa fa-plus pr-2"></i>Add
                        New Entry</a>
                    -->
            <div class="listing">
                <div class="col-12 mb-2">
                    <div class="mr-2 d-inline-block">
                        <span class="legends-purple colorboxsquare"></span>{{ $t('AuditableRecords') }}
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends_profile colorboxsquare"></span> {{ $t('LeaveHoliday') }}
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends_low colorboxsquare"></span>{{ $t('UnPaidLeaveRecords') }}
                    </div>
                    <!-- <div class="mr-2 d-inline-block">
                        <span class="legends_medium colorboxsquare"></span> {{$t('TimeSheet')}}
                    </div> -->
                    <div class="mr-2 d-inline-block">
                        <span class="legends_high colorboxsquare"></span>{{ $t('HolidayLeaveAuditableRecords') }}
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="lg-pausedresumed colorboxsquare"></span>{{ $t('PausedResumedTrackEntries') }}

                    </div>
                </div>
                <div>
                    <a id="btnAddNewEntry" class="btn btn-success formbtn mr-2 text-dark" v-on:click="showAddTaskModal"
                        name="btnAddNewEntry" :title="$t('AddNewEntry')" style="float: right;"><i
                            class="fa fa-plus pr-2"></i>{{ $t('AddNewEntry') }}</a>
                </div>
            </div>
            <div class="table-responsive" style="min-height:400px;">
                <table class="table table-bordered dt-responsive nowrap mt-0">
                    <thead class="thead-bg">
                        <tr>
                            <th class="align-middle d-none">Project Id</th>
                            <th>Project Name</th>
                            <th class="align-middle d-none">Task Id</th>
                            <th class="align-middle d-none">Task Name</th>
                            <th v-for="(item, index) in HeaderList" :key="index">
                                {{ getPreviousDay(item) }}
                            </th>
                            <th id="RowTotal">Total</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody v-if="computedTimesheetList.length > 0">

                        <tr v-for="(dataItem, dataIndex) in computedTimesheetList" :key="dataIndex">
                            <td class="align-middle d-none">{{ dataItem['ProjectId'] }}</td>
                            <td>{{ dataItem['ProjectName'] }}</td>
                            <td class="align-middle d-none">{{ dataItem['TaskId'] }}</td>
                            <td class="align-middle d-none">{{ dataItem['TaskDetail'] }}</td>
                            <td class="align-middle" v-for="(item, index) in HeaderList" :key="index">
                                <input type="text" v-model="dataItem[item]" @input="ChangeCurrentDateTime(data, this)">
                            </td>
                            <td class="align-middle">{{ rowSum(dataItem) }}</td>
                            <td class="align-middle"><button class="fa fa-times text-danger"
                                    @click="SingleDeleteData(dataIndex)"></button> </td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr>
                            <td colspan="8" class="text-center">No records found</td>
                        </tr>
                    </tbody>
                    <tfoot v-if="computedTimesheetList.length > 0">
                        <td class="align-middle d-none"></td>
                        <td>Total:</td>
                        <td class="align-middle d-none"></td>
                        <td class="align-middle d-none"></td>
                        <td v-for="(tData, index) in totalData" :key="index">{{ tData.value }}</td>
                        <td>{{ SumOfFooterRows }}</td>

                    </tfoot>
                </table>
            </div>

            <!-- <a id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></a> -->
            <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" v-on:click="CopyLastWeek"
                name="" :title="$t('CopyToOtherUser')" style="float: right;"><i class="fa fa-copy pr-2"></i>{{
                    $t('CopyToOtherUser') }} </a>
            <!--      <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" onclick="" name="" title='Copy Last week'><i class="fa fa-copy pr-2"></i>Copy Last week</a> -->
            <a id="btnSaveAsDraft" isapprover="0" class="btn btn-warning formbtn text-dark mr-2"
                v-on:click="SaveAsDraft(1, 0)" name="" :title="$t('SaveAsDraft')" style="float: right;"><i
                    class="fa fa-save pr-2"></i>{{ $t('SaveAsDraft') }}</a>
            <!-- <a id="btnSendForApproval" isapprover="0" class="btn btn-success text-white formbtn mr-2" onclick="" title='SendForApproval'><i class="fa fa-send pr-2"></i>SendForApproval</a> -->
        </div>

        <AddTaskTimeSheetModal v-if="addTaskShow" v-bind:store="store" v-on:close="addTaskShow = false"
            v-on:saveTaskEntry="saveTaskEntry"></AddTaskTimeSheetModal>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import { getPreviousDay } from "@/assets/common/vue-common.js"
import AddTaskTimeSheetModal from "../TimeTrack/modals/AddTaskTimesheetModal.vue"
import {
    userTimerInfo
} from "../../stores/TimeTrack"
export default
    {
        components: {
            AddTaskTimeSheetModal
        },
        setup() {
            const store = userTimerInfo()

            return {
                store
            }
        },
        data() {
            return {
                getPreviousDay,
                dateselect: '',
                //formData: [],
                deletedArray: [],
                totalData: [],
                DataLoaded: 0,
                noRecord: false,
                TotalRecords: 0,
                isLoading: false,
                TimesheetList: [],
                HeaderList: [],
                UserId: null,
                StartDate: null,
                EndDate: null,
                DisplayWeekDate: "This Week",
                StartWeekDay: null,
                totalSeconds: 0,
                //Headers: [],
                addTaskShow: false,
                SumOfRows: [],
                SumOfFooterRows: 0,
                FormSchema: [
                    {
                        layoutType: "triple",
                        Data: [
                            {
                                astype: "SlotField",
                                label: "",
                                name: "slotdate",
                                value: "",
                                placeholder: "Date Range",
                            },
                            {
                                astype: "SelectField",
                                label: "User",
                                name: "user_id",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                },
                                validationRules: {
                                    //"required": true
                                },
                            },
                            {
                                astype: "SlotField",
                                label: "",
                                name: "slotbutton",
                                value: "",
                                placeholder: "",

                            },
                        ]
                    }
                ]
            }
        },
        created: async function () {
            var VueObj = this;
            this.DataLoaded = 1;
            await VueObj.GetStartEndDatesNew();
            await VueObj.GetUsersForTimesheet();
            VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
            await VueObj.FetchData();

        },
        mounted: async function () {
            var VueObj = this;
            console.log('mounted')
        },
        computed: {
            computedTimesheetList() {
                return this.TimesheetList
            },
            filteredDeletedArray() {
                return this.deletedArray;
            },
            rowSums() {
                return this.TimesheetList.map(dataItem => this.rowSum(dataItem));
            },
            footerSums() {
                return this.totalData.map(dataItem => this.footerSums(dataItem));
            }
        },
        methods: {
            footerSums(columnName) {
                const excludedColumns = ["ProjectId", "ProjectName", "TaskDetail", "TaskId"];
                let totalSeconds = 0;

                if (!excludedColumns.includes(columnName)) {
                    this.dataArray.forEach((dataItem) => {
                        const value = dataItem[columnName];
                        if (/^([01]\d|2[0-3]):([0-5]\d)$/.test(value)) {
                            const [hours, minutes] = this.getHoursAndMinutes(value);
                            totalSeconds += hours * 3600 + minutes * 60;
                        }
                    });
                }

                const totalHours = Math.floor(totalSeconds / 3600);
                const totalMinutes = Math.floor((totalSeconds % 3600) / 60);

                return String(totalHours).padStart(2, "0") + ":" + String(totalMinutes).padStart(2, "0");
            },
            columnSums() {
                const excludedColumns = ["ProjectId", "ProjectName", "TaskDetail", "TaskId"];
                const columnSums = {};

                this.HeaderList.forEach((header) => {
                    if (!excludedColumns.includes(header)) {
                        const columnValues = this.TimesheetList.map((dataItem) => {
                            const value = dataItem[header];
                            return /^([01]\d|2[0-3]):([0-5]\d)$/.test(value) ? this.getHoursAndMinutes(value) : 0;
                        });

                        const totalSeconds = columnValues.reduce((sum, [hours, minutes]) => sum + hours * 3600 + minutes * 60, 0);
                        const totalHours = Math.floor(totalSeconds / 3600);
                        const totalMinutes = Math.floor((totalSeconds % 3600) / 60);

                        columnSums[header] = String(totalHours).padStart(2, "0") + ":" + String(totalMinutes).padStart(2, "0");
                    }
                });

                return columnSums;
            },
            rowSum(dataItem) {
                var rowcount = 0;
                return Object.values(dataItem).filter(dataItem => dataItem !== "ProjectId" && dataItem !== "ProjectName" && dataItem !== "TaskDetail" && dataItem != "TaskId" && dataItem !== "RowTotal")
                    .reduce((sum, value) => {
                        const isDatetimeColumn = /^([01]\d|2[0-3]):([0-5]\d)$/.test(value);
                        if (isDatetimeColumn) {
                            let [hours, minutes] = value != null ? value.split(":") : "00:00".split(":");
                            rowcount += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                        }
                        let hours = Math.floor(rowcount / 3600);
                        let minutes = Math.floor((rowcount % 3600) / 60);
                        sum = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                        return sum;
                    }, 0);
            },
            saveTaskEntry(e) {
                debugger
                var vm = this;
                var obj = {}
                vm.HeaderList.map((column) => {
                    obj[column] = '00:00'
                })
                Object.assign(obj, e)
                vm.TimesheetList.push(obj)
                this.addTaskShow = false
            },
            showAddTaskModal() {
                this.addTaskShow = true
            },
            ChangeCurrentDateTime(e, a, t, t1) {
                debugger
                this.SumRowColTotalList();
                this.SumOfRows();
                this.SumOfFooterRows();
                // if (e.row[e.column.COLUMN_NAME].includes(":") && e.row[e.column.COLUMN_NAME].split(":")[1] != "" && e.row[e.column.COLUMN_NAME].split(":")[1] != null) {
                //     this.SumRowColTotalList();
                //     this.SumOfRows();
                //     this.SumOfFooterRows();
                // }
            },
            async GetUsersForTimesheet() {
                var VueObj = this;
                VueObj.isLoading = true;
                var url = `companyId=${VueObj.GetUserInfo.CompanyId}&loggedUserId=${VueObj.GetUserInfo.ID}&userType=CA&userId=${VueObj.FormSchema[0].Data[1].value}&moduleName=`;
                await DataService.GetUsersForTimesheet(url)
                    .then(function (response) {

                        response.data.result.forEach(item => {
                            VueObj.obj = {
                                name: item.username,
                                value: item.useR_ID
                            }
                            VueObj.FormSchema[0].Data[1].config.options.push(VueObj.obj);
                        });

                        VueObj.isLoading = false;
                    });
            },
            OnChange: function () {
                var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
                if (checkCount > 0) {
                    $('#DeleteMultiple').removeClass('disabled');
                }
                else {
                    $('#DeleteMultiple').addClass('disabled');
                }
            },
            async onSubmit(formfields, e) {
                var vm = this;
                vm.FetchData();
            },
            GetStartEndDates: async function (event, id, a) {
                //console.log("date", this.dateselect)
                var VueObj = this;
                let url = `DateSpan=thisweek&report=`;
                await DataService.GetStartEndDate(url).then(response => {
                    VueObj.StartDate = response.data.dateStart;
                    VueObj.EndDate = response.data.dateEnd;
                    VueObj.DisplayWeekDate = "";
                    VueObj.DisplayWeekDate = response.data.displayText;
                    VueObj.StartWeekDay = response.data.startWeekDay;
                });
                //VueObj.FormSchema[0].Data[0].placeholder = "Date Range: " + VueObj.DisplayWeekDate;
                var offset = 1;
                switch (VueObj.StartWeekDay.toLowerCase()) {
                    case 'tuesday':
                        offset = 2;
                        break;
                    case 'wednesday':
                        offset = 3;
                        break;
                    case 'thursday':
                        offset = 4;
                        break;
                    case 'friday':
                        offset = 5;
                        break;
                    case 'satureday':
                        offset = 6;
                        break;
                    case 'sunday':
                        offset = 0;
                        break;
                    default:
                        offset = 1;
                        break;
                }

                var today = new Date(this.dateselect);
                if (today == '') {
                    today = new Date();
                }
                var currentDay = today.getDay() - offset;
                var startdate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay);
                var enddate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay + 6);
                var dateTime = VueObj.$options.filters.formatDate(startdate) + '-' + VueObj.$options.filters.formatDate(enddate);

                VueObj.StartDate = VueObj.$options.filters.formatDate(startdate);
                VueObj.EndDate = VueObj.$options.filters.formatDate(enddate);
                VueObj.DisplayWeekDate = "";
                VueObj.DisplayWeekDate = dateTime;

            },
            GetStartEndDatesNew: async function (thisweek) {
                var VueObj = this;
                let url = `DateSpan=thisweek&report=`;
                await DataService.GetStartEndDate(url).then(response => {
                    VueObj.StartDate = response.data.dateStart;
                    VueObj.EndDate = response.data.dateEnd;
                    VueObj.DisplayWeekDate = response.data.displayText;
                    VueObj.StartWeekDay = response.data.startWeekDay;
                });
                VueObj.FormSchema[0].Data[0].placeholder = "Date Range: " + VueObj.DisplayWeekDate;
                VueObj.DisplayWeekDate = "This Week";

            },
            FetchData: async function () {
                var VueObj = this;
                VueObj.isLoading = true;
                var fDate = VueObj.StartDate;
                var tDate = VueObj.EndDate;
                var inputData = []
                // var url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&userId=${VueObj.FormSchema[0].Data[1].value}&companyId=270936`;
                var url = `companyId=${VueObj.GetUserInfo.CompanyId}&userId=${VueObj.FormSchema[0].Data[1].value}&dateFrom=${fDate}&dateTo=${tDate}`;
                await DataService.ListTimesheetForWeek(url).then(response => {
                    if (response.data != null && response.data != "" && response.data.length > 0) {
                        let headerList1 = Object.keys(response.data[0]).filter(key => key !== 'ProjectId' && key !== 'ProjectName' && key !== 'TaskId' && key !== 'TaskDetail');
                        VueObj.isLoading = false;
                        var count;
                        this.HeaderList = headerList1;

                        VueObj.TotalRecords = response.data.length;
                        VueObj.TimesheetList = response.data;
                        var scol = 0;
                        var totalSeconds = 0;
                        VueObj.SumRowColTotalList()
                    }
                    else {
                        VueObj.noRecord = true;
                        VueObj.TimesheetList = [];
                    }

                });
            },
            SumRowColTotalList: async function () {
                var VueObj = this;
                VueObj.totalData = [];
                //var sum1 = 0;
                VueObj.HeaderList.forEach((column) => {
                    var colcount = 0;
                    if (column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail' && column.COLUMN_NAME !== 'TaskId' && column.COLUMN_NAME !== 'ProjectId') {
                        //console.log('column', column)
                        VueObj.TimesheetList.forEach((data, index) => {
                            //scol += value;
                            // const [hours, minutes] = data[column.COLUMN_NAME] != null ? data[column.COLUMN_NAME].split(":") : "00:00".split(":");
                            const [hours, minutes] = data[column] != null ? data[column].split(":") : "00:00".split(":");
                            colcount += parseInt(hours!=null && hours.trim() === '' ? 0 : hours) * 3600 + parseInt(minutes!=null && minutes.trim() === '' ? 0 : minutes) * 60;

                        });
                        //console.log('colcount', colcount)
                        let hours = Math.floor(colcount / 3600);
                        let minutes = Math.floor((colcount % 3600) / 60);
                        var formattedTime = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                    }
                    VueObj.totalData.push({ value: formattedTime });
                    VueObj.totalSeconds = colcount;
                });
                console.log("totalData", VueObj.totalData);
                console.log("SumOfRows", VueObj.SumOfRows);
                var footerSum = 0;
                VueObj.totalData.forEach((item, index) => {
                    let [hours, minutes] = item.value != null ? item.value.split(":") : "00:00".split(":");
                    footerSum += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                    //footerSum = footerSum + item.value;
                });
                let hours = Math.floor(footerSum / 3600);
                let minutes = Math.floor((footerSum % 3600) / 60);
                VueObj.SumOfFooterRows = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                console.log("after rowcount 1st time", VueObj.TimesheetList);


                //VueObj.FetchRowColTotalList();

            },
            SingleDeleteData: function (index) {
                var vm = this;
                var fDate = vm.StartDate;
                var tDate = vm.EndDate;

                vm.TimesheetList.forEach((item) => {
                    debugger
                    var iserowxist = vm.TimesheetList.indexOf(item);

                    if (iserowxist == index) { // && vm.TimesheetList.splice(index).length > 0
                        //console.log('delete', item);
                        if (!item.IsAddNewEntry) {
                            vm.deletedArray.push(item);
                        }
                        vm.TimesheetList.splice(index, 1);
                    }

                });
            },
            SaveAsDraft: async function (isDraft, isSendForApproval) {
                debugger
                var VueObj = this;
                let obj = {
                    TimesheetList: VueObj.TimesheetList,
                    deletedData: VueObj.deletedArray,
                    userId: VueObj.FormSchema[0].Data[1].value,
                    dateFrom: VueObj.StartDate,
                    dateTo: VueObj.EndDate
                }
                var formData = new FormData();
                formData.append("jsonData", JSON.stringify(obj));
                //await DataService.ManageCategory(obj).then(response => {

                // if (isDraft == 1) {
                //     if (!GetPrivilege("/Timesheet/SaveBonus", true, false)) {
                //         return false;
                //     }
                // }
                // else {
                //     if (!GetPrivilege("/Timesheet/SubmitAuditTimesheet", true, false)) {
                //         return false;
                //     }
                // }

                await DataService.SaveTimeSheetEntry(obj).then(response => {
                    //console.log('SaveAsDraft', response);
                });
            },
            CopyLastWeek: async function () {
                // if (!GetPrivilege("/Timesheet/PasteLastWeekTimesheet", true, false)) {
                //     return false;
                // }

                VueObj.confirmR("MessageForCopyLastWeekTimeEntries", true, ("MessageForCopyLastWeekTimeEntries"), function () {
                    let url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${VueObj.FormSchema[0].Data[1].value}`;
                    DataService.PasteLastWeekTimesheet(url).then(response => {
                        VueObj.FetchData();
                    });
                });

            },
        }
    }
</script>
