<template lang="en">
<div>
    <div>
      
        <ValidationObserver ref="form" v-slot="{ handleSubmit }">
            <form v-on:submit.prevent="handleSubmit(submit)" ref="form">
                <!-- <button type="button" class="close" style="background: none;color: #fff !important;" @click="$emit('close',{breakTimer:false,modalClose:false})"><em aria-hidden="true" class="fa fa-times"></em></button> -->
               
                <div class="w-100 d-flex dynamic-multiselect" id="divModalTimetrack">
                    <ValidationProvider :rules="'required'" v-slot="{ errors }">
                        <multiselect :placeholder="$t('searchProject')" class="form-group tt_ddr_1 col border-0 " @input="taskByProject" v-model="formRecord.projectId" :options="store.getProjectsByUser" label="projectName" track-by="projectId" :show-labels="false" id="exampleFormControlSelect1" :class="store.getProjectDetails.trackType=='Break' ? 'tt_disabled':''">
                        </multiselect>
                        <span class="val_msg"></span>
                    </ValidationProvider>
                </div>

                <multiselect :placeholder="$t('searchTask')" class="form-control tt_ddr col border-0 " id="exampleFormControlSelect2" v-model="formRecord.taskId" :options="store.getTasksFromProject" label="firstAttribute" track-by="value" :show-labels="false"  :class="store.getProjectDetails.trackType=='Break' ? 'tt_disabled':''">
                    <template slot="option" slot-scope="props" :disabled="true">
                        <!-- parentValue -->
                        <div class="option__desc"><span class="option__title" :disabled="true">{{
                                                props.option.firstAttribute }}</span>
                            <label class="float-right text-light px-2 ml-1 taskcount">{{ props.option.subTasksCount }}</label>
                            <label class="float-right"> {{ props.option.thirdAttribute }}% </label>
                        </div>
                    </template>

                </multiselect>

                <div class="row flex-row-reverse">

                    <div class="text-end text-right col-12">
                        <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}</button>
                        <button @click="$emit('close')" type="button" class="btn btn-danger" style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</button>
                    </div>

                </div>
            </form>
        </ValidationObserver>
    </div>

</div>
</template>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import Multiselect from 'vue-multiselect'

import moment from "moment"
import {
    timeToSeconds,
    getSecondsFromDate
} from "@/assets/common/vue-common.js"
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
extend('required', {
    validate(value) {
        console.log("check", value)
        return {
            required: true,
            valid: ['', null, value.length > 0].indexOf(value) === -1
        };
    },
    message: '{_field_} is required',
    computesRequired: true
});
export default defineComponent({
    name: "Add Task",
    components: {
        Multiselect,
        ValidationProvider,
        ValidationObserver,
    },
    props: {
        store: {
            type: Object
        },

    },
    data() {
        return {
formRecord:{
    projectId:'',
    taskId:''
}
        }
    },
    setup() {

    },

    created:async function () {
        await this.store.getProjects()
    },
    methods: {
        taskByProject() {
            if (this.formRecord.projectId) {
                this.store.loadTasks(this.formRecord.projectId.projectId)
            } 
            this.store.$state.tasks = []
        },
         
        submit: function () {
            debugger
             this.$emit('saveTaskEntry',{'TaskId':this.formRecord.taskId.value,"ProjectId":this.formRecord.projectId.projectId,"ProjectName":this.formRecord.projectId.projectName, "TaskDetail":this.formRecord.taskId.firstAttribute, "IsAddNewEntry": "yes"})
        },

    }

})
</script>
